import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { PaymentManageComponent } from './payment-manage/payment-manage.component';//功能配置
const routes: Routes = [
    {
        path: 'payment-manage', component: PaymentManageComponent,
    },
];

export const MeansPaymentRouting: ModuleWithProviders = RouterModule.forChild(routes);
